import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';

import EmployeesTable from './Employess/EmployeesTable';
import Cards from './Cards/Cards';
import SideBar from './SideBar/SideBar';
import ContactsTable from './ContactDisplay/ContactApi';



const Dashboard =() => {

    return(

        <BrowserRouter>
            <SideBar />
            <div>
                <Route exact path="/dashboard" component={Cards}></Route>
                <Route exact path="/dashboard/employees" component={EmployeesTable}></Route>
                <Route exact path="/dashboard/messages" component={ContactsTable}></Route>
            </div>
        </BrowserRouter>
       )
}

export default Dashboard;
