import React from 'react';
import './SideBar.css';
import { Link } from 'react-router-dom';

const SideBar = () => {
    return (
		<nav id="sideNav">
        <ul id="sidebtn">
            <li><Link className="sidebuttons" to="/dashboard">HOME</Link></li>
            <li><Link className="sidebuttons" to="/dashboard/employees">Employees</Link></li>
            <li><Link className="sidebuttons" to="/dashboard/messages">Messages</Link></li>
        </ul>
    </nav>
    )
}

export default SideBar
