import React, {useState, useEffect} from 'react';
import MaterialTable from 'material-table';
import {Container} from './EmployeeElements'

const EmployeesTable = () => {
    
    const [data, setData] = useState([])
    const columns = [
        { title: "Emp_Id", field: "Emp_Id" },
        { title: "Name", field: "Emp_Name" },
        { title: "Designation", field: "Designation" },
        { title: "Phone", field: "Emp_Phone" },
        { title: "Email", field: "Emp_Email" },
        { title: "Address", field: "Emp_Address" },
    ]

    useEffect(()=>{
        fetch("/api/employeeslist")
        .then(resp=>resp.json())
        .then(resp=>setData(resp))
    })
    useEffect(() => {
		document.title = 'Employees - Details';
	});
    return (
        <Container>
            <MaterialTable 
                title="Messages Details"
                data={data}
                columns={columns}
                options={{
                    exportButton: true
                }}
            />
        </Container>
    )
}

export default EmployeesTable;