import React,{Component} from 'react';
import axios from 'axios';
import ViewContact from './MessagesDisplay';

const formurl = "/api/contactslist";


class ShowContacts extends Component{
    constructor(){
        super()

        this.state={
            loading: false,
            orders:''
        }
    }

    render(){
        console.log("class",this.props)
        if (!this.state.orders){
            return <center><img src="https://media.giphy.com/media/kUTME7ABmhYg5J3psM/giphy.gif" alt="loader-gif" border="0"/></center>
        }
        return(
            <div className="container">
                <ViewContact orderdata={this.state.orders}/>
            </div>
        )
    }


    async componentDidMount(){
        let response =  await axios.get(`${formurl}`)
        this.setState({orders:response.data})
    }


    /*componentDidMount(){
        axios.get(`${bookurl}`)
        .then((response) =>  {this.setState({hotelist:response.data})})
    }*/

}

export default ShowContacts;